.login {
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: white;
}
.login_logo {
  margin: 20px auto;
  object-fit: contain;
  width: 100px;
}
.login_container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}
.login_container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}
.login_container > form > h5 {
  margin-bottom: 5px;
  text-align: left;
}
.login_container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
  outline: none;
  border: 1px solid gray;
  padding-left: 5px;
}
.login_container > p {
  margin-top: 15px;
  font-size: 12px;
}
.login_signInButton {
  background-color: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
}
.login_registerButton{
    border-radius: 2px;
    width:100% ;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}
button:hover{
    cursor: pointer;
}
