@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@400;500;600;700;800&display=swap);
* {
  margin: 0;
  padding: 0;

  scroll-behavior: smooth;
}
body {
  margin: 0;
  background-color: rgb(234, 237, 237);
  font-family: "Baloo Bhai 2", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}
.header_logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}
.header_search {
  display: flex;
  flex: 1 1;
  align-items: center;
  border-radius: 24px;
}
.header_searchInput {
  height: 12px;
  border: none;
  width: 100%;
  padding: 10px;
}
.header_searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #cd9042;
}
.header_nav {
  display: flex;
  justify-content: space-evenly;
}
.header_option {
  display: flex;
  flex-direction: column;
  color: white;
  margin: 0 10px;
}
.header_optionLineOne {
  font-size: 10px;
}
.header_optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}
.header_optionBasket{
    display: flex;
    align-items: center;
    color: white;
}
.header_basketCount {
    margin: 0 10px;
}

.home{
    display: flex;
    justify-content: center;
    margin: 0 auto;
    max-width: 1500px;

}
.home_image{
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    -webkit-mask-image: linear-gradient(to bottom , rgba(0,0,0,1), rgba(0,0,0,0));
            mask-image: linear-gradient(to bottom , rgba(0,0,0,1), rgba(0,0,0,0));
}
.home_row{
    display: flex;
    
    z-index: 1;
    margin: 0 5px;
}
.backtotop{
    width: 100%;
    border: none;
    color: white;
    background-color:  #37475A;
    height: 40px;
    font-weight: bold;
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.backtotop:hover{
    background-color: #485769;
}
.product{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: flex-end;
   margin: 10px;
   padding: 20px;
   width: 100%;
   max-height: 400px;
   min-width: 100px;
   background-color: white;
   transition: all 300ms ease-in;
   z-index: 1; 
}
.product:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    /* padding: 15px; */
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product_info{
    height: 100px;
    margin-bottom: 15px;
    text-align: left;
}
.product_price{
    margin-top: 5px;
}
.product > button{
    background: #f8c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29 ;
    color: #111;
    outline: none;
    padding: 5px;
}
.product > button:hover{
    cursor: pointer;
}
.product_rating{
    display: flex;

}
.login {
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: white;
}
.login_logo {
  margin: 20px auto;
  object-fit: contain;
  width: 100px;
}
.login_container {
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}
.login_container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}
.login_container > form > h5 {
  margin-bottom: 5px;
  text-align: left;
}
.login_container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
  outline: none;
  border: 1px solid gray;
  padding-left: 5px;
}
.login_container > p {
  margin-top: 15px;
  font-size: 12px;
}
.login_signInButton {
  background-color: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
}
.login_registerButton{
    border-radius: 2px;
    width:100% ;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}
button:hover{
    cursor: pointer;
}

.checkout_ad{
    width:100%;
    margin-bottom: 10px;

}
.checkout{
display: flex;
padding: 20px;
background-color: white;
height: -webkit-max-content;
height: max-content;

}
.checkout_title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #ddd;
    border-radius: 3px;

}
.subtotal_gift{
    display: flex;
    align-items: center;
}
.subtotal_gift>input{
    margin-right: 5px;

}
.subtotal>button{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color:#a88734 #9c7e31 #846a29 ;
    color: #111
}
.subtotal>button:hover{
cursor: pointer;
}
.checkoutProduct {
  display: flex;
  margin: 20px 0;
  width: 100%;
  align-content: start;
}

.checkoutProduct_info {
  padding-left: 20px;
}

.checkoutProduct_info > button {
  background-color: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734;
  color: #111;
  padding: 5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.checkoutProduct_image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}
.checkoutProduct_price {
  text-align: left;
}
.checkoutProduct_rating {
  display: flex;
}
.checkoutProduct_title {
  font-size: 17px;
  font-weight: 800;
}

.payment {
  background-color: white;
}
.payment_container > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}

.payment_container > h1 a {
  text-decoration: none;
}
.payment_section {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgray;
  
}
.payment_title {
  flex: 0.2 1;
  border-right: 1px solid lightgray;
}
.payment_address,
.payment_items,
.payment_details {
  flex: 0.8 1;
}
.payment_details>form>div>button{
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  font-weight: bolder;
  margin-top: 10px;
  color: #111;
  border-color:#a88734 #9c7e31 #846a29 ;
}
.footer {
  background-color: #232f3e;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 10px;
  justify-content: center;
        
}
.footer_options{
    display: flex;
    flex-direction: row;
    margin: 10px 0;
    align-items: flex-start;
}
.footer_row {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
.footer_row > span:hover{
    text-decoration: underline;
    cursor: pointer;
}
.footer_logo{
    margin: 10px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.footer > hr{
    border: 1px solid gray;
}
.footer_logo>img{
    width: 100px;

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.orders {
    padding: 20px 80px;
}

.orders > h1 {
    margin: 30px 0;
}
.order {
  padding: 40px;
  margin: 20px 0;
  border: 1px solid lightgray;
  background-color: white;
  position: relative;
}

.order_id {
  position: absolute;
  top: 40px;
  right: 20px;
}

.order_total {
  font-weight: 500;
  text-align: right;
}

